/**
 *
 * Component to attach files to the storage
 * Author : Daniel Contreras Aladro
 * Date : 2017-02-21
 *
 */
declare var JSZipUtils: any;

import {
  Component,
  OnInit,
  OnDestroy,
  AfterViewInit,
  Input,
  Output,
  EventEmitter
} from '@angular/core';
import { DatePipe } from '@angular/common';

import { Observable, Subscription } from 'rxjs/Rx';

import { environment } from '../../../environments/environment';

import tinymce from 'tinymce/tinymce';
import 'tinymce/themes/modern/theme';

// Plugins
import 'tinymce/plugins/paste/plugin';
import 'tinymce/plugins/searchreplace/plugin';

import { TranslateService } from '@ngx-translate/core';

import * as $ from 'jquery/dist/jquery';

import { ZendeskService } from '../../services/zendesk.service';
import { PayaraService } from '../../services/payara.service';

import { Attachment } from '../../classes/attachment';

@Component({
  selector: 'app-add-file',
  templateUrl: './add-file.component.html',
  styleUrls: ['./add-file.component.css']
})
export class AddFileComponent implements OnInit, AfterViewInit, OnDestroy {
  /**
   * Properties and objects of the component
   */
  environment = environment;
  tinymce = tinymce;

  groups: any = [];
  staticFileButtons: any = [
    /*
    {
      title:this.translate.instant('Monitoring Data'),
      url:'get-monitoring-configuration.json',
      group:'general',
      uploaded:false,
      type:'json'
    },
    */
    {
      title: this.translate.instant('Domain Log'),
      url: 'view-log',
      group: 'general',
      uploaded: false,
      type: 'log'
    },
    {
      title: this.translate.instant('Health Checks'),
      url:
        'configs/config/server-config/health-check-service-configuration/list-historic-healthchecks.json',
      group: 'reports',
      uploaded: false,
      type: 'health'
    },
    {
      title: this.translate.instant('Request Traces'),
      url:
        'configs/config/server-config/request-tracing-service-configuration/list-historic-requesttraces.json',
      group: 'reports',
      uploaded: false,
      type: 'traces'
    }
  ];

  fileButtons: any = [];

  filesLoaded: boolean = false;
  filesSaved: boolean = false;

  @Input() title: string;

  @Output() saved = new EventEmitter<Attachment>();
  @Output() removed = new EventEmitter<string>();

  private sub: Subscription;

  errorMessage: string;
  successMessage: string;
  loadingMessage: string;
  loadingMessageXml: string;

  elementId: string = 'domainEditor';
  isVisibleEditor: boolean = true;
  xmlLoaded: boolean = false;
  xmlLoading: string;
  xmlColor: string = 'warning';

  editor: any;

  otherfiles: any = [];

  /**
   * ngAfterViewInit - AfterViewInit method of the component
   */
  ngAfterViewInit() {}
  /**
   * constructor - Constructor of the component
   */
  constructor(
    private translate: TranslateService,
    private zendeskService: ZendeskService,
    private payaraService: PayaraService,
    private datePipe: DatePipe
  ) {}
  /**
   * ngOnDestroy - OnDestroy method of the component
   */
  ngOnDestroy() {
    $('span.ng-tooltip').hide();
    if (this.sub) this.sub.unsubscribe();
  }
  /**
   * filter - Method to filter the list buttons to show on the screen
   *
   * @param {any[]}   buttons Array of buttons to filter
   * @param {string}  group   String to use to filter buttons
   */
  filter(buttons: any[], group: string) {
    return buttons.filter(data => data.group === group);
  }
  /**
   * ngOnInit - OnInit method of the component
   */
  ngOnInit() {
    this.groups.push('general');
    this.groups.push('reports');
    this.fileButtons = Object.assign([], this.staticFileButtons);
    this.payaraService.getServerInstances().then(
      responseData => {
        if (responseData !== undefined && responseData !== null) {
          this.filesLoaded = true;
          let responseArray = Object.keys(responseData);
          for (let prop in responseArray) {
            if (responseArray[prop] === 'server') {
              this.groups.push('DAS');
              this.fileButtons.push({
                title:
                  this.translate.instant('JVM Report') +
                  ': ' +
                  responseArray[prop],
                url:
                  'servers/server/' +
                  responseArray[prop] +
                  '/generate-jvm-report.json?type=summary',
                group: 'DAS',
                uploaded: false,
                type: 'json'
              });
              this.fileButtons.push({
                title:
                  this.translate.instant('Thread Dump') +
                  ': ' +
                  responseArray[prop],
                url:
                  'servers/server/' +
                  responseArray[prop] +
                  '/generate-jvm-report.json?type=thread',
                group: 'DAS',
                uploaded: false,
                type: 'json'
              });
              this.fileButtons.push({
                title: 'Log: ' + responseArray[prop],
                url:
                  'logs_' +
                  responseArray[prop] +
                  '.zip?contentSourceId=LogFiles&target=' +
                  responseArray[prop] +
                  '&restUrl=' +
                  window.location.protocol +
                  '//' +
                  window.location.hostname +
                  ':' +
                  window['globalPort'] +
                  '/management/domain',
                group: 'DAS',
                file: 'logs_' + responseArray[prop] + '.zip',
                uploaded: false,
                type: 'zip'
              });
              this.fileButtons.push({
                title:
                  this.translate.instant('Monitoring Data') +
                  ': ' +
                  responseArray[prop],
                url:
                  'get.json?pattern=' + responseArray[prop] + '*&monitor=true',
                group: 'DAS',
                uploaded: false,
                type: 'json'
              });
            } else {
              this.payaraService.getInstanceStatus(responseArray[prop]).then(
                responseData => {
                  this.groups.push('Instance: ' + responseArray[prop]);
                  if (
                    responseArray[prop] === 'server' ||
                    (responseData !== undefined &&
                      responseData !== null &&
                      responseData === 'RUNNING')
                  ) {
                    this.fileButtons.push({
                      title:
                        this.translate.instant('JVM Report') +
                        ': ' +
                        responseArray[prop],
                      url:
                        'servers/server/' +
                        responseArray[prop] +
                        '/generate-jvm-report.json?type=summary',
                      group: 'Instance: ' + responseArray[prop],
                      uploaded: false,
                      type: 'json'
                    });
                    this.fileButtons.push({
                      title:
                        this.translate.instant('Thread Dump') +
                        ': ' +
                        responseArray[prop],
                      url:
                        'servers/server/' +
                        responseArray[prop] +
                        '/generate-jvm-report.json?type=thread',
                      group: 'Instance: ' + responseArray[prop],
                      uploaded: false,
                      type: 'json'
                    });
                    this.fileButtons.push({
                      title: 'Log: ' + responseArray[prop],
                      url:
                        'Log: ' +
                        responseArray[prop] +
                        '.zip?contentSourceId=LogFiles&target=' +
                        responseArray[prop] +
                        '&restUrl=' +
                        window.location.protocol +
                        '//' +
                        window.location.hostname +
                        ':' +
                        window['globalPort'] +
                        '/management/domain',
                      group: 'Instance: ' + responseArray[prop],
                      file: 'Log: ' + responseArray[prop] + '.zip',
                      uploaded: false,
                      type: 'zip'
                    });
                    this.fileButtons.push({
                      title:
                        this.translate.instant('Monitoring Data') +
                        ': ' +
                        responseArray[prop],
                      url:
                        'get.json?pattern=' +
                        responseArray[prop] +
                        '*&monitor=true',
                      group: 'Instance: ' + responseArray[prop],
                      uploaded: false,
                      type: 'json'
                    });
                  }
                },
                error => {
                  this.showErrorMessage('Error! Service Unavailable');
                }
              );
            }
          }
        }
      },
      error => {
        this.showErrorMessage('Error! Service Unavailable');
      }
    );
    this.translate.get('LOADING').subscribe((res: string) => {
      this.xmlLoading = res;
      this.discardXml();
    });
  }
  /**
   * showErrorMessage - Method to show an error message
   *
   * @param {string}   message Message to show
   */
  showErrorMessage(message: string) {
    this.translate.get(message).subscribe((res: string) => {
      this.errorMessage = res;
      let timer = Observable.timer(1000, 50);
      this.sub = timer.subscribe(t => {
        this.errorMessage = '';
        this.sub.unsubscribe();
      });
      this.loadingMessage = null;
      this.successMessage = '';
    });
  }
  /**
   * fileUploaded - Method to show a message when a file is uploaded successfully
   *
   * @param {any}   file Object with the file attached
   */
  fileUploaded(file: any) {
    this.saved.emit(file);
    this.filesSaved = true;
    this.loadingMessage = null;
    this.successMessage = '';
    this.translate.get('File added successfully!').subscribe((res: string) => {
      this.successMessage = res;
    });
    let timer = Observable.timer(1000, 50);
    this.sub = timer.subscribe(t => {
      this.successMessage = '';
      this.sub.unsubscribe();
    });
  }
  /**
   * searchFile - Event to get the data for the file when the button is pressed
   *
   * @param {any}  fileButton Object with the data of the fileButton
   */
  searchFile(fileButton: any) {
    this.errorMessage = '';
    this.loadingMessage = '';
    this.successMessage = '';
    if (fileButton.uploaded) {
      this.removed.emit(fileButton.title);
      fileButton.uploaded = false;
    } else {
      this.loadingMessage =
        this.translate.instant('Loading file ...') + fileButton.title;
      if (fileButton.url && fileButton.url !== '') {
        switch (fileButton.type) {
          case 'log':
            this.getFile(fileButton.url, false, response => {
              if (response !== false) {
                this.addFile(
                  response['_body'],
                  fileButton.title + '.log',
                  'application/octet-stream',
                  response => {
                    if (response) {
                      this.fileUploaded(response);
                      fileButton.uploaded = true;
                    } else {
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = '';
                    }
                  }
                );
              } else {
                fileButton.uploaded = false;
                this.showErrorMessage('Error! Service Unavailable');
              }
            });
            break;
          case 'traces':
            this.payaraService.postFile(fileButton.url).then(
              responseData => {
                if (
                  responseData !== false &&
                  responseData.message !== '' &&
                  responseData.exit_code !== 'FAIULRE' &&
                  responseData.message.indexOf('is not enabled') < 0
                ) {
                  this.addFile(
                    responseData.message,
                    fileButton.title + '.txt',
                    'application/octet-stream',
                    response => {
                      if (response) {
                        this.fileUploaded(response);
                        fileButton.uploaded = true;
                      } else {
                        fileButton.uploaded = false;
                        this.loadingMessage = null;
                        this.successMessage = '';
                      }
                    }
                  );
                } else {
                  fileButton.uploaded = false;
                  this.showErrorMessage('Empty data');
                }
              },
              error => {
                fileButton.uploaded = false;
                this.showErrorMessage('Error! Service Unavailable');
              }
            );
            break;
          case 'health':
            this.getFile(fileButton.url, false, response => {
              let healthData = JSON.parse(response['_body']);
              if (
                healthData !== false &&
                healthData.extraProperties !== undefined &&
                healthData.extraProperties.historicmessages.length > 0
              ) {
                this.addFile(
                  JSON.stringify(healthData.extraProperties.historicmessages),
                  fileButton.title + '.txt',
                  'application/octet-stream',
                  response => {
                    if (response) {
                      this.fileUploaded(response);
                      fileButton.uploaded = true;
                    } else {
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = '';
                    }
                  }
                );
              } else {
                fileButton.uploaded = false;
                this.showErrorMessage('Empty data');
              }
            });
            break;
          case 'json':
            this.getFile(fileButton.url, false, response => {
              if (
                response !== false &&
                response.json().message !== undefined &&
                (response.json().message !== '' ||
                  response.json().exit_code === 'SUCCESS')
              ) {
                this.addFile(
                  fileButton.url.indexOf('get.json?') < 0
                    ? response.json().message
                    : JSON.stringify('' + response.json().children),
                  fileButton.title + '.txt',
                  'application/octet-stream',
                  response => {
                    if (response) {
                      this.fileUploaded(response);
                      fileButton.uploaded = true;
                    } else {
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = '';
                    }
                  }
                );
              } else {
                fileButton.uploaded = false;
                this.showErrorMessage('Empty data');
              }
            });
            break;
          case 'zip':
            this.getFile(fileButton.url, true, response2 => {
              if (response2 !== false) {
                this.addFile(
                  new File([response2], fileButton.file, {
                    type: 'application/zip'
                  }),
                  fileButton.file,
                  'application/zip',
                  result => {
                    if (result) {
                      this.fileUploaded(result);
                      fileButton.uploaded = true;
                    } else {
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = '';
                    }
                  }
                );
              } else {
                fileButton.uploaded = false;
                this.showErrorMessage('Empty data');
              }
            });
            break;
        }
      }
    }
  }
  /**
   * cleanFiles - Method to clean the add file buttons and enable it
   */
  cleanFiles() {
    this.otherfiles = [];
    this.filesSaved = false;
    this.filesLoaded = false;
    this.xmlLoaded = false;
    this.isVisibleEditor = false;
    this.xmlColor = 'default';
    this.xmlLoading = 'NO';
    for (let button of this.fileButtons) {
      button.uploaded = false;
    }
  }
  /**
   * searchFile - Event to get the data for the file when the button is pressed
   *
   * @param {any}      content Object with file's data
   * @param {string}   name String with file's name
   * @param {string}   type String with file's mimetype
   * @param {any}      back Callback function to return sync value
   */
  addFile(content: any, name: string, type: string, back: any) {
    let fileDate = this.datePipe.transform(new Date(), 'yyyy-MM-ddTHH:mm:ss');
    if (!(content instanceof String)) {
      this.zendeskService.addNewFile(content, fileDate + '_' + name, type).then(
        responseData => {
          if (responseData !== undefined && responseData !== null) {
            back(JSON.parse(responseData).upload);
          }
        },
        error => {
          this.showErrorMessage('Error! Service Unavailable');
          back(false);
        }
      );
    } else {
      if (content.indexOf('offline') < 0) {
        this.zendeskService.addNewFile(content, name, type).then(
          responseData => {
            if (responseData !== undefined && responseData !== null) {
              back(JSON.parse(responseData).upload);
            }
          },
          error => {
            this.showErrorMessage('Error! Service Unavailable');
            back(false);
          }
        );
      } else {
        this.showErrorMessage('Instance seems to be offline');
        back(false);
      }
    }
  }
  /**
   * searchFile - Event to get the data for the file when the button is pressed
   *
   * @param {string}   url String with the endpoint to call
   * @param {boolean}  parse Boolean to know if parse data is needed
   * @param {any}      back Callback function to return sync value
   */
  getFile(url: string, parse: boolean, back: any) {
    if (parse) {
      JSZipUtils.getBinaryContent(
        this.payaraService.connectionData.filesURL + url,
        (err, data) => {
          if (err) {
            this.showErrorMessage('Error! Service Unavailable');
            back(false);
          } else {
            back(data);
          }
        }
      );
    } else {
      this.payaraService.getFile(url).then(
        responseData => {
          if (responseData !== undefined && responseData !== null) {
            back(responseData);
          } else {
            this.showErrorMessage('Empty data');
            back(false);
          }
        },
        error => {
          this.showErrorMessage('Error! Service Unavailable');
          back(false);
        }
      );
    }
  }
  /**
   * showEditor - Function to show/hide xml editor
   */
  showEditor() {
    const loadingText = this.translate.instant('LOADING');
    if (this.xmlLoading === loadingText) {
      this.isVisibleEditor = false;
      this.xmlColor = 'default';
      this.xmlLoading = 'NO';
    } else if (this.xmlLoading === 'YES') {
      this.removed.emit('Domain.xml');
      this.isVisibleEditor = false;
      this.xmlColor = 'default';
      this.xmlLoading = 'NO';
    } else if (this.xmlLoading === 'NO') {
      this.isVisibleEditor = true;
      this.xmlColor = 'warning';
      this.xmlLoading = loadingText;
    }
  }
  /**
   * discardXml - Function to discard changes made to xml file shown
   */
  discardXml() {
    this.loadFileData();
    this.xmlLoaded = false;
    this.showEditor();
  }
  /**
   * loadFileData - Function to discard changes made to xml file shown
   */
  loadFileData() {
    this.getFile(
      'configs/config/server-config/zendesk-support-configuration/get-domain-xml.json',
      false,
      response => {
        if (response !== undefined && response !== null) {
          this.tinymce.remove('#' + this.elementId);
          this.editor = null;
          let initObject = {
            relative_urls: false,
            remove_script_host: false,
            document_base_url: null,
            skin_url: '../assets/skins/lightgray',
            selector: '#' + this.elementId,
            plugins: ['paste', 'searchreplace'],
            elementpath: false,
            height: 300,
            menubar: false,
            toolbar: false,
            statusbar: false,
            forced_root_block: false,
            setup: editor => {
              this.editor = editor;
            }
          };
          if (this.environment.production) {
            initObject.relative_urls = false;
            initObject.remove_script_host = false;
            initObject.document_base_url =
              window.location.protocol +
              '//' +
              window.location.hostname +
              ':' +
              window['globalPort'] +
              '/resource/payara_support/zendesk/';
            initObject.skin_url = './assets/skins/lightgray';
          }
          this.tinymce.init(initObject);
          this.tinymce.activeEditor.setContent(
            response
              .json()
              .message.replace(/>/g, '&gt;')
              .replace(/</g, '<br>&lt;')
              .replace('<br>', '')
          );
        }
      }
    );
  }
  /**
   * saveXml - Function to save changes made to xml file shown
   */
  saveXml() {
    this.loadingMessageXml =
      this.translate.instant('Loading file ...') + ' Domain.xml';
    this.addFile(
      this.editor
        .getContent()
        .replace(/&gt;/g, '>')
        .replace(/&lt;/g, '<')
        .replace(/<br \/>/g, '\n'),
      'Domain.xml',
      'application/binary',
      response => {
        this.fileUploaded(response);
        this.isVisibleEditor = false;
        this.xmlColor = 'success';
        this.xmlLoading = 'YES';
        this.loadingMessageXml = null;
        this.tinymce.activeEditor.setMode('readonly');
      }
    );
  }
  /**
   * otherFile - Method to attach one or more files selected from the file system
   *
   * @param {any}   event Object with the event data of the files selected
   */
  otherFile(event: any) {
    const fileList: FileList = event.target.files;
    if (fileList.length > 0) {
      for (let i = 0; i < fileList.length; i++) {
        const file: File = fileList[i];
        if (file.size < 20971520) {
          let formData: FormData = new FormData();
          formData.append('upload', file, file.name);
          this.otherfiles.push(file);
          this.addFile(formData, file.name, 'application/binary', response => {
            this.fileUploaded(response);
          });
        } else {
          this.showErrorMessage(
            'Error! File ' + file.name + ' size bigger than 20 MB'
          );
        }
      }
    } else {
      this.showErrorMessage('Error! File not exist');
    }
  }
  /**
   * removeFile - Method to remove a file to attach
   *
   * @param {any}   file File object with the file to remove
   */
  removeFile(file: any) {
    this.otherfiles = this.otherfiles.filter(data => data.name !== file.name);
    this.removed.emit(file.name);
    $('span.ng-tooltip').hide();
  }
}



